import moment from 'moment'
import {dateDMY, dateFull, isFloat} from 'services'
import {stockMovementValidation} from './moduleValidation'

export const addLine =  ({itemList, setItemList,expiryDateSelected,moduleName}) => {
    let newList = [...itemList]
    const lastList = newList[newList.length -1]

    switch (moduleName) {
        case 'stockTake':
            newList.push({
                grade:lastList?.grade,
                expiryDate:expiryDateSelected?.value,
                expiryDateText:dateFull({noTime:true, date:expiryDateSelected?.value})
            })
            setItemList(newList)
            break;
        case 'stockMovement':
            newList.push({
                qty: '',
                expiryDate:expiryDateSelected?.value,
                expiryDateText:dateFull({noTime:true, date:expiryDateSelected?.value})
            })
            setItemList(newList)
            break;
    }
}

export const changeValues = ({e, idx, itemList,setItemList,setTotalQty, ddName, selected,moduleName}) => {
    let changedList = [...itemList]
    if(ddName){
        switch(ddName){
            case 'ripeness':
            changedList[idx].ripeness = selected
            if(moduleName === 'stockMovement') changedList[idx].grade = selected
            changedList[idx].ripenessIsEmpty = false
            changedList[idx].noteIsEmpty = false
            changedList[idx].qtyIsEmpty = false
            setItemList(changedList)
            break
            case 'grade':
                if((selected?.label === 'Waste' || selected?.label === 'Semi Waste') && moduleName === 'stockTake'){
                    changedList[idx].ripenessIsEmpty = false
                    changedList[idx].ripeness = ''
                }
                if((selected?.label === 'Waste' || selected?.label === 'Semi Waste') && moduleName === 'stockMovement'){
                    changedList[idx].ripenessIsEmpty = false
                    changedList[idx].ripeness = ''
                }
            changedList[idx].note = ''
            changedList[idx].grade = selected
            changedList[idx].gradeIsEmpty = false
            setItemList(changedList)
            break
            case 'outbound':
            changedList[idx].outbound = selected
            changedList[idx].outboundIsEmpty = false
            setItemList(changedList)
            break
            case 'date':
            let date = moment(selected).valueOf()
            changedList[idx].expiryDate = date
            changedList[idx].expiryDateText = dateDMY({date})
            setItemList(changedList)
            break
        }
    return
    }


    const value = e.target.value
    const name = e.target.name
    switch(name){
        case 'note':
        changedList[idx].note = e.target.value
        changedList[idx].noteIsEmpty = false
        setItemList(changedList)
        break

        case 'qty':
        let regex = /^[0-9.]+$/
        if ((regex.test(value) === false && value !== "")) {
            return;
        }
        changedList[idx].qty = e.target.value
        changedList[idx].qtyIsEmpty = false

        case 'weight':
            if (name === 'weight' || name === 'weightToCompare') {
                let regex = /^[0-9\.\x0]+$/
                if ((regex.test(value) === true && value !== "")) {
                    let newValue = 0;
                let arr = value?.split('.')
                let number = arr[0];
                if(number >= 0) number = parseInt(number)
                let commaNumber = arr[1];
                
                if ((commaNumber !== null && commaNumber !== undefined)) {
                    if (commaNumber.length > 2) {
                        //jika dibelakang koma ada lebih dari 2 angka
                        var sliceNumber = commaNumber.slice(0, 2);
                        newValue = `${number}.${sliceNumber}`
                    } else {
                        newValue = `${number}.${commaNumber}`
                    }
                } else {
                    newValue = parseInt(number)
                }
                
                    //fill weight qty
                    if(name === 'weight'){
                        changedList[idx].qty = newValue
                        changedList[idx].qtyIsEmpty = false
                    }
                    if(name === 'weightToCompare'){
                        changedList[idx].qtyToCompare = newValue
                        changedList[idx].qtyToCompareIsEmpty = false
                    }
        
                }
                else{
                    //fill weight qty
                    if(name === 'weight'){
                        changedList[idx].qty = ''
                    }
                    if(name === 'weightToCompare'){
                        changedList[idx].qtyToCompare = ''
                    }   
                }
            }

        let totalQty = 0
        changedList.map(item =>{
            if(item.qty) totalQty = totalQty += isFloat(item?.qty) 
        })

        setTotalQty(totalQty)
        setItemList(changedList)
    }
}

export const submitValidation =({ module, moduleName, setConfirmation , itemList, setItemList, setSingleViewData,supplierNameSelected, inboundDateSelected, expiryDateSelected,areaMovement}) => {
    let isEmpty = []
    let checkItemList = []
    let jobPost = Object.assign({}, JSON.parse(JSON.stringify(module)))
    let totalQty = 0
    let po =[]
    const supplierName = supplierNameSelected?.value
    const inboundDate = inboundDateSelected?.value
    const expiryDate = expiryDateSelected?.value 
           const {moduleIsEmpty, moduleCheckItemList,moduleTotalQty} = stockMovementValidation({itemList,jobPost,moduleName,areaMovement})
           isEmpty = moduleIsEmpty
           checkItemList = moduleCheckItemList
           totalQty = moduleTotalQty

           po = jobPost.jobPost.list[0].poList.filter(p => {
            let condition = null 
            if(!expiryDate) condition = p.supplierName === supplierName && p.inboundDate === inboundDate
            if(expiryDate) condition = p.supplierName === supplierName && p.inboundDate === inboundDate && expiryDate === p.expiryDate
    
            if(condition) return p
        })
        
    setItemList(checkItemList)

    let updateInitialWeight =po[0].initialWeight - totalQty
    jobPost.jobPost.list[0].poList = [po[0]]    
    jobPost.jobPost.list[0].poList[0].initialWeight = updateInitialWeight
    jobPost.jobPost.list[0].poList[0].totalQty = updateInitialWeight.toString()
    jobPost.jobPost.list[0].poList[0].initialWeightFormatted = updateInitialWeight.toString()
    jobPost.jobPost.list[0].poList[0].itemList = itemList
    jobPost.jobPost.list[0].poList[0].supplierName = supplierNameSelected?.value
    jobPost.jobPost.list[0].poList[0].inboundDate = inboundDateSelected?.value ? inboundDateSelected?.value : jobPost.jobPost.list[0].poList[0].inboundDate
    jobPost.jobPost.list[0].poList[0].expiryDate = expiryDateSelected?.value ? expiryDateSelected?.value :  jobPost.jobPost.list[0].poList[0].expiryDate
    if (!isEmpty.includes(true)) {
        setSingleViewData(jobPost)
        setConfirmation({ show: true, name: 'submit' })
    }
    else {
        setConfirmation({ show: false, name: null })
    }
}

export const updateSummaryAndModuleEntry = ({singleViewData, moduleName,moduleEntry,setLocalEntryData,summary, resetSummary}) => {
    let updatedLocalEntryData = {...moduleEntry}
    let updatedSummary = [...summary]
    if(singleViewData?.jobPost?.list?.length){
        //sku
        const skuUuid = singleViewData.jobPost.list[0].skuUuid
        //po
        const updatedPo = singleViewData.jobPost.list[0].poList[0]
        const supplierName = singleViewData.jobPost.list[0].poList[0].supplierName
        const inboundDate = singleViewData.jobPost.list[0].poList[0].inboundDate
        const expiryDate = singleViewData.jobPost.list[0].poList[0].expiryDate
        const skuName = singleViewData.jobPost.list[0].skuName
        let qty = 0
        updatedPo.itemList.map(item => {
            if(item?.grade?.label === skuName && moduleName === 'stockTake') qty += isFloat(item.qty)
            if(moduleName === 'stockMovement') qty += isFloat(item.qty)
        })
        if(moduleName === 'stockTake') updatedPo.initialWeight = qty
        // update summaries
        updatedSummary = updatedSummary.map(data => {
            if(data.skuUuid === skuUuid){
                if(moduleName === 'stockMovement') data.totalQty -= qty
                if(moduleName === 'stockTake') data.totalQty = qty
                return data
            }
            return data
        })
        //update local entry
        if(supplierName && inboundDate && expiryDate){
            let list = updatedLocalEntryData?.jobPost?.list?.map(sku => {
                let po = sku.poList.map(po => {
                            if(sku.skuUuid === skuUuid && po.supplierName === supplierName && po.inboundDate === inboundDate && po.expiryDate === expiryDate){
                                po =  updatedPo
                                if(moduleName !== 'stockTake') po.itemList = []
                                return po
                            }
                            return po
                            })
                sku.poList = po
                return sku
            })
            updatedLocalEntryData.jobPost.list = list
        }

        if(supplierName && inboundDate){
            let list = updatedLocalEntryData?.jobPost?.list?.map(sku => {
                let po = sku.poList.map(po => {
                if(sku.skuUuid === skuUuid && po.supplierName === supplierName && po.inboundDate === inboundDate){
                    po =  updatedPo
                    if(moduleName !== 'stockTake') po.itemList = []
                    return po
                }
                return po
                })
                sku.poList = po
                return sku
            })
            updatedLocalEntryData.jobPost.list = list
        }
        setLocalEntryData(updatedLocalEntryData)
        return updatedSummary
    }    
}

export const getPoDataByHeader = ({moduleName, supplierNameSelected,inboundDateSelected,expiryDateSelected,moduleEntry,skuNumber,setItemList}) => {
    const supplierName = supplierNameSelected?.value
    const inboundDate = inboundDateSelected?.value 
    const expiryDate = expiryDateSelected?.value
    let initialWeight = 0
    let list = []
    if(moduleEntry){
        moduleEntry.jobPost.list.map(sku => {
            sku.poList.map(po => {
                const sameSKUNumber = sku.skuNumber === skuNumber
                const sameSupplierName = po.supplierName === supplierName
                const sameInboundDate = po.inboundDate === inboundDate
                const sameExpiryDate = po.expiryDate === expiryDate

                let condition = null
                if(!expiryDate) condition = sameSKUNumber && sameSupplierName && sameInboundDate
                if(expiryDate) condition = sameSKUNumber && sameSupplierName && sameInboundDate && sameExpiryDate

                if(condition){
                    initialWeight = po.initialWeight
                    list = po.itemList
                }
            })
        })
    }
    return {initialWeight}
}

export const getSVSource = ({moduleEntry, skuNumber}) => {
    let supplierName = []
    let module = null
    let category = null
    let gradeList = []
    let uom = null
    let totalWeight = 0
    let parameterList = []
    
    if(moduleEntry?.jobPost?.list?.length && skuNumber){

        //sku filter by sku name
        module = Object.assign({}, JSON.parse(JSON.stringify(moduleEntry)))
        const skuList = module?.jobPost?.list?.filter(data => data.skuNumber === skuNumber)

        module.jobPost.list = skuList
        if(skuList.length){
            const item = module?.jobPost?.list[0]
            category = item.category
            totalWeight = item.totalQty
            uom = item.unitOfMeassures
            gradeList = item.gradeList


            //filter dropdown, supplier name & expiry date
            const poList = item.poList
            
            supplierName = poList.map(data => data?.supplierName)
            supplierName = supplierName.filter((item,idx,arr) => {
                if(arr.indexOf(item) === idx) return {label:item, value:item}
            })
            
            supplierName = supplierName.map(data => ({value: data, label:data}))

            parameterList = poList.map(po => {
                return {
                    supplierName: po.supplierName,
                    inboundDate:po.inboundDate,
                    expiryDate:po.expiryDate,
                    poNumber:po.wmsPoSkuCode
                }
            })
        }
        
    }
    return {supplierName, module,category,uom, gradeList,totalWeight,parameterList}
}

export const setOptionValue = ({
    module,
    moduleName,
    setTotalQty,
    setItemList,
    setSupplierNameSelected,
    setInboundDateSelected,
    setExpiryDateSelected,
    setMoveAll,
    setInboundDateOption,
    setExpiryDateOption,
    supplierName,
    parameterList
    }) => {
    setSupplierNameSelected(supplierName[0])
    let updatedInbound = parameterList.map(date => {
        if(date.supplierName === supplierName[0].value) return {label:date.inboundDate ? `${dateFull({noTime:true,date:date.inboundDate})} - ${date.poNumber}` : `${date.inboundDate} - ${date.poNumber}`, value:date.inboundDate}
    })
    updatedInbound = updatedInbound.filter(dt => dt !== undefined)
    setInboundDateOption(updatedInbound)
    setInboundDateSelected(updatedInbound[0])
    
    let updatedExpiryDate = parameterList.map(date => {
        if(date.supplierName === supplierName[0].value && date.inboundDate === updatedInbound[0].value) return {label:(date.expiryDate ? `${dateFull({noTime:true,date:date.expiryDate})} - ${date.poNumber}` : `${date.expiryDate} - ${date.poNumber}`), value:date.expiryDate}
    })
    updatedExpiryDate = updatedExpiryDate.filter(dt => dt !== undefined)
    setExpiryDateOption(updatedExpiryDate)
    setExpiryDateSelected(updatedExpiryDate[0])

    if(moduleName === 'stockTake'){
        if(module){
            const list = module.jobPost.list[0].poList[0]?.itemList
            if(list?.length){
                setTotalQty(list[0].qty)
                setItemList(list)
            }
        }
    }

    if(typeof(setMoveAll) === 'function') setMoveAll()

}

export const filterByDropdown = ({
    module,
    setItemList,
    parameterList,
    ddName,
    selected,
    setSupplierNameSelected,
    setInboundDateSelected,
    setExpiryDateSelected,

    setInboundDateOption,
    setExpiryDateOption,
    supplierNameSelected,

    inboundDateSelected,
    expiryDateSelected,
    moduleEntry,
    skuNumber
    }) => {
    let list = []
    list = moduleEntry?.jobPost?.list.filter(sku => sku.skuNumber === skuNumber)
    list = list[0].poList.map(po => {
        let item = po.itemList.map(item => {
            item.qty = po.initialWeight
            return item
        })
        po.itemList = item 
        return po
    })
    if(ddName === 'supplierName'){
        setSupplierNameSelected(selected)
        let updatedInbound = parameterList.map(date => {
            if(date.supplierName === selected.value && date.inboundDate) return {label:(date.inboundDate ? `${dateFull({noTime:true,date:date.inboundDate})} - ${date.poNumber}` : `${date.inboundDate} - ${date.poNumber}`), value:date.inboundDate}
        })
        updatedInbound = updatedInbound.filter(d => d!== undefined)
        setInboundDateOption(updatedInbound)
        setInboundDateSelected(updatedInbound[0])
        
        let updatedExpiryDate = parameterList.map(date => {
            if(date.supplierName === selected.value && date.inboundDate === updatedInbound[0].value) return {label:date.expiryDate ? `${dateFull({noTime:true,date:date.expiryDate})} - ${date.poNumber}` : `${date.expiryDate} - ${date.poNumber}`, value:date.expiryDate}
        })
        updatedExpiryDate = updatedExpiryDate.filter(d => d!== undefined)
        setExpiryDateOption(updatedExpiryDate)
        setExpiryDateSelected(updatedExpiryDate[0])

    } 
    if(ddName === 'inboundDate') {
        setInboundDateSelected(selected)
        let updatedExpiryDate = parameterList.map(date => {
            if(date.supplierName ===supplierNameSelected.value && date.inboundDate === selected.value) return {label:date.expiryDate ? `${dateFull({noTime:true,date:date.expiryDate})} - ${date.poNumber}` : `${date.expiryDate} - ${date.poNumber}`, value:date.expiryDate}
        })
        updatedExpiryDate = updatedExpiryDate.filter(d => d!== undefined)
        setExpiryDateOption(updatedExpiryDate)
        setExpiryDateSelected(updatedExpiryDate[0])
    }

    if(ddName === 'expiryDate'){
        setExpiryDateSelected(selected)
    }
}

export const deleteRow = ({idx, itemList,setItemList, setTotalQty}) => {
    const newItem = itemList.filter((item, i) =>  i !== idx)
    let qty = 0
    newItem.map(item => qty += item.qty)
    setTotalQty(qty)
    setItemList(newItem)
}

export const updateInitialWeight = ({moduleEntry,supplierNameSelected,inboundDateSelected,expiryDateSelected,skuNumber,setItemList,setTotalQty}) => {
    let list = []
    list = moduleEntry?.jobPost?.list.filter(sku => sku.skuNumber === skuNumber)
    list = list[0].poList.map(po => {
        let item = po.itemList.map(item => {
            item.qty = po.initialWeight
            return item
        })
        po.itemList = item 
        return po
    })

    list = list.filter(po => po.supplierName === supplierNameSelected.value && po.inboundDate === inboundDateSelected.value && po.expiryDate === expiryDateSelected.value)
    if(list.length){
        setItemList(list[0].itemList)
        setTotalQty(list[0].itemList.qty)
    }

}